import { reqseckillList } from "../../util/request"

// 设置数据
const state = {

    // 定义 菜单列表
    seckilllist: [],

}

// 进行更改数据
const mutations = {

    // 更改菜单
    changeSeckillList(state, arr) {
        state.seckilllist = arr
    },


}

// 进行发送请求
const actions = {
    // 发送请求
    actionSeckillList(store) {
        reqseckillList().then(res => {
            store.commit('changeSeckillList', res.data.list)
        })
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    SeckillGetter(state) {
        return state.seckilllist;
    },
}

// 暴露出去
export default {
    state, mutations, actions, getters, namespaced: true
}


