import { reqGoodsList, reqGoodsCount } from "../../util/request"

// 设置数据
const state = {

    // 定义 菜单列表
    goodslist: [],

    // 获取总数 
    number: 0,

    // 设置每页 展示条数
    pagesize: 5,

    // 当前页
    newpage: 1

}

// 进行更改数据
const mutations = {
    // 更改菜单
    changeGoodsList(state, arr) {
        state.goodslist = arr
    },
    changecount(state, num) {
        state.number = num
    },

    // 设置 当前页
    changpage(state, num) {
        state.newpage = num
    }
}

// 进行发送请求
const actions = {
    // 发送请求
    actionGoodsList(store,bool) {
        
        //  不要忘记传参数啊啊啊 啊

        let params = bool?{}:{
            page: state.newpage,
            size: state.pagesize
        }

        reqGoodsList(params).then(res => {
            // 这个地方为了解决 第二页删除 的bug 
            
            /*
                * 第二页删除的时候会data.list 会获取到null
                * 我们需要进行处理 如果 他获取到 null 就让他变成 空数组 
                * 并且需要进行重新加载 那我们 在做一个判断 如果 存放列表的数据还有就证明前面的
                * 一页还有数据 我们需要 进行-1 并重新进行 发送请求 获取数据
            */ 
     


            if (res.data.list == null) {
                res.data.list = []
                if (state.newpage > 1 && state.goodslist.length != 0) {
                    state.newpage = state.newpage - 1;
                    store.dispatch('actionGoodsList')
                    return
                }
            }

            store.commit('changeGoodsList', res.data.list)
        })
    },

    // 请求总数 
    actionGoodsCount(store) {
        reqGoodsCount().then(res => {
            store.commit('changecount', res.data.list[0].total)
        })
    },

    actionGoodsPage(store, num) {
        store.commit('changpage', num)
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    goodsGetter(state) {
        return state.goodslist;
    },
    goodsCount(state) {
        return state.number;
    },
    // 设置每页显示的条数
    goodsPage(state) {
        return state.pagesize;
    },
    goodsnewpage(state) {
        return state.newpage;
    }
}


// 暴露出去
export default {
    state, mutations, actions, getters, namespaced: true
}


