// 这里 只是 将所有 的 仓库组件 整合在一起 然后 统一管理 


import Vue from "vue"
import Vuex from "vuex"
Vue.use(Vuex)

import { state, getters, mutations } from "./mutations"
import { actions } from "./actions"
import menu from "./modules/menulist"
import role from "./modules/rolelist"
import admin from "./modules/adminlist"
import cate from "./modules/cate"
import specs from "./modules/specs"
import goods from "./modules/goods"
import member from "./modules/member"
import banner from "./modules/banner"
import seckill from "./modules/seckill"


export default new Vuex.Store({
    state, mutations, getters, actions, modules: { menu, role, admin, cate, specs, goods,member,banner,seckill }
})


