// 商品添加接口
export const reqCateAdd = (form) => {

    let data = new FormData()
    for (let i in form) {
        data.append(i, form[i])
    }

    return axios({
        url: baseurl + "/api/cateadd",
        method: "post",
        data: data
    })
}

