import {reqMenuList} from "../../util/request"

// 设置数据
const state = {
    // 定义 菜单列表
    menulist :[]
}

// 进行更改数据
const mutations = {
    // 更改菜单
    changeMenuList(state,arr){
        state.menulist = arr
    }
}

// 进行发送请求
const actions = {
    // 发送请求
    actionMenuList(store){
        reqMenuList().then(res=>{
            store.commit('changeMenuList', res.data.list)
        })
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    menuGetter(state){
        return state.menulist;
    }
}


// 暴露出去
export default {
    state,mutations,actions,getters,namespaced: true
}


