import { reqUserList, reqUserCount } from "../../util/request"

// 设置数据
const state = {

    // 定义 菜单列表
    adminlist: [],

    // 获取总数 
    number: 0,

    // 设置每页 展示条数
    pagesize: 5,

    // 当前页
    newpage: 1

}

// 进行更改数据
const mutations = {
    // 更改菜单
    changeAdminList(state, arr) {
        state.adminlist = arr
    },
    
    changecount(state, num) {
        state.number = num
    },

    // 设置 当前页
    changpage(state, num) {
        state.newpage = num
    }


}

// 进行发送请求
const actions = {
    // 发送请求
    actionAdminList(store) {
        reqUserList({
            page: state.newpage,
            size: state.pagesize
        }).then(res => {
            
            // 这个地方为了解决 第二页删除 的bug 
            /*
                * 第二页删除的时候会data.list 会获取到null
                * 我们需要进行处理 如果 他获取到 null 就让他变成 空数组 
                * 并且需要进行重新加载 那我们 在做一个判断 如果 存放列表的数据还有就证明前面的
                * 一页还有数据 我们需要 进行-1 并重新进行 发送请求 获取数据
            */ 
           
            if (res.data.list == null) {
                res.data.list = []
                if (state.newpage > 1 && state.adminlist.length != 0) {
                    state.newpage = state.newpage - 1;
                    store.dispatch('actionAdminList')
                    return
                }
            }

            store.commit('changeAdminList', res.data.list)
        })
    },

    // 请求总数 
    actionAdminCount(store) {
        reqUserCount().then(res => {
            store.commit('changecount', res.data.list[0].total)
        })
    },

    actionAdminPage(store, num) {
        store.commit('changpage', num)
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    adminGetter(state) {
        return state.adminlist;
    },
    adminCount(state) {
        return state.number;
    },
    // 设置每页显示的条数
    adminPage(state) {
        return state.pagesize;
    },
    adminnewpage(state) {
        return state.newpage;
    }
}


// 暴露出去
export default {
    state, mutations, actions, getters, namespaced: true
}


