import { reqMemberList } from "../../util/request"

// 设置数据
const state = {
    // 定义 菜单列表
    memberlist: []
}

// 进行更改数据
const mutations = {
    // 更改菜单
    changeMemberList(state, arr) {
        state.memberlist = arr
    }
}

// 进行发送请求
const actions = {
    // 发送请求
    actionMemberList(store) {
        reqMemberList().then(res => {
           
            store.commit('changeMemberList', res.data.list)
        })
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    MemberGetter(state) {
        return state.memberlist;
    }
}


// 暴露出去
export default {
    state, mutations, actions, getters, namespaced: true
}


