import { reqbannerList } from "../../util/request"

// 设置数据
const state = {
    // 定义 菜单列表
    bannerlist: []
}

// 进行更改数据
const mutations = {
    // 更改菜单
    changeBannerList(state, arr) {
        state.bannerlist = arr
    }
}

// 进行发送请求
const actions = {
    // 发送请求
    actionBannerList(store) {
        reqbannerList().then(res => {
            store.commit('changeBannerList', res.data.list)
        })
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    BannerGetter(state) {
        return state.bannerlist;
    }
}


// 暴露出去
export default {
    state, mutations, actions, getters, namespaced: true
}


