<template>
  <el-button class="submit" plain @click="add" v-model="isShow.flag">添 加</el-button>
</template>

<script>
export default {
  methods: {
    add() {
      this.$emit("addnutton");
    },
  },

  data() {
    return {
      isShow: {
        flag: false,
        // 为true 的时候变成 修改
        edit: true,
      },
    };
  },
};
</script>

<style>
.submit {
  background-color: #4fc08d;
  color: #fff;
}

</style>


