import { reqCateList } from "../../util/request"

// 设置数据
const state = {
    // 定义 菜单列表
    catelist: []
}

// 进行更改数据
const mutations = {
    // 更改菜单
    changeCateList(state, arr) {
        state.catelist = arr
    }
}

// 进行发送请求
const actions = {
    // 发送请求
    actionCateList(store) {
        reqCateList({istree:true}).then(res => {
            store.commit('changeCateList', res.data.list)
        })
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    CateGetter(state) {
        return state.catelist;
    }
}


// 暴露出去
export default {
    state, mutations, actions, getters, namespaced: true
}


