import Vue from 'vue'
import Router from 'vue-router'
import store from "../store/index"

Vue.use(Router)

function guard(path11, next) {

  /*
    * 进行 路由独享守卫 判断 后台返回的路由 跟跟 用户设置的权限 判断是否 一致  一致的时候 即可 展示 
  */
 
  let a = JSON.stringify(store.state.userInfo.menus_url)
  if (a.includes(path11)) {
    next()
    return
  }else{
    next("/")
  }
}


export let childrenRouter = [
  {
    path: "/home",
    name: "首页",
    component: () => import("../page/home/home"),
  },
  {
    path: "/menu",
    name: "菜单管理",
    component: () => import("../page/menu/menu"),
    beforeEnter: (to, from, next) => {
      guard("/menu", next)
    },

  }, {
    path: "/role",
    name: "角色管理",
    component: () => import("../page/roles/role"),
    beforeEnter: (to, from, next) => {
      guard("/role", next)
    },

  }, {
    path: "/manage",
    name: "管理员管理",
    component: () => import("../page/admin/manage"),
    beforeEnter: (to, from, next) => {
      guard("/manage", next)
    },

  }, {
    path: "/cate",
    name: "商品分类",
    component: () => import("../page/cate/cate"),
    beforeEnter: (to, from, next) => {
      guard("/cate", next)
    },
  }, {
    path: "/spec",
    name: "商品规格",
    component: () => import("../page/specs/spec"),
    beforeEnter: (to, from, next) => {
      guard("/spec", next)
    },
  }, {
    path: "/goods",
    name: "商品管理",
    component: () => import("../page/goods/goods"),
    beforeEnter: (to, from, next) => {
      guard("/goods", next)
    },
  }, {
    path: "/member",
    name: "会员管理",
    component: () => import("../page/member/member"),
    beforeEnter: (to, from, next) => {
      guard("/member", next)
    },
  }, {
    path: "/banner",
    name: "轮播图管理",
    component: () => import("../page/banner/banner"),
    beforeEnter: (to, from, next) => {
      guard("/banner", next)
    },
  }, {
    path: "/seckill",
    name: "秒杀活动",
    component: () => import("../page/seckill/seckill"),
    beforeEnter: (to, from, next) => {
      guard("/seckill", next)
    },
  }
]

let router = new Router({
  routes: [
    {
      path: "/login",
      name: "登录",
      component: () => import("../page/login/login")
    }, {
      path: "/",
      component: () => import("../page/index/index"),
      children: [
        ...childrenRouter
      ],
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.path === "/login") {
    next()
    return
  }

  // 判断本地缓存有没有缓存 有缓存的话就继续进行
  // 这里 因为 userInfo 的内容是由 本地缓存取出来的  所以 是字符串 需要在 状态层进行处理

  // 如果 本地缓存中 info 字段 为 空了 那么我们就需要进行 一遍 跳转  登录页面 
  if (sessionStorage.getItem("info") == "null" || sessionStorage.getItem("info") == "") {
    next("/login")
    return
  }

  // 如果我们 在状态层 查找到了 token  那就证明 已经登录了
  if (store.state.userInfo.token) {
    next()
    return
  }
  

  next("/login")
})

export default router


