import { reqRoleList } from "../../util/request"

const state = {
    list: [],
}

const mutations = {
    changerole(state, arr) {
        state.list = arr
    },
}

const actions = {
    actionrole(store) {
        reqRoleList().then(res => {
            store.commit('changerole', res.data.list)
        })
    },

}

const getters = {
    gettersrole(state) {
        return state.list
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
    namespaced: true
}



