import { reqSpecsList, reqSpecsCount } from "../../util/request"

// 设置数据
const state = {

    // 定义 菜单列表
    specslist: [],

    // 获取总数 
    number: 0,

    // 设置每页 展示条数
    pagesize: 5,

    // 当前页
    newpage: 1

}

// 进行更改数据
const mutations = {
    // 更改菜单
    changeSpecsList(state, arr) {
        state.specslist = arr
    },

    changecount(state, num) {
        state.number = num
    },

    // 设置 当前页
    changpage(state, num) {
        state.newpage = num
    }
}

// 进行发送请求
const actions = {
    // 发送请求
    actionSpecsList(store,bool) {
        
        /*
            * 这里 为了能够过 获取到商品 管理 的时候 商品属性 可以全部 获取到 所以 做了 一个
            * 处理 ， 传参数 就 获取 5 条 不传参数 就全部 都可获取 
        */ 
        let params = bool?{}:{
            page: state.newpage,
            size: state.pagesize
        }

        reqSpecsList(params).then(res => {
        
            // 这个地方为了解决 第二页删除 的bug 
            /*
                * 第二页删除的时候会data.list 会获取到null
                * 我们需要进行处理 如果 他获取到 null 就让他变成 空数组 
                * 并且需要进行重新加载 那我们 在做一个判断 如果 存放列表的数据还有就证明前面的
                * 一页还有数据 我们需要 进行-1 并重新进行 发送请求 获取数据
            */
           
            if (res.data.list.length == 0) {
                if (state.newpage > 1 && state.specslist.length != 0) {
                    state.newpage = state.newpage - 1;
                    store.dispatch('actionSpecsList')
                    return
                }
            }

            // // 进行处理 数据转换成数组
            res.data.list.forEach(item => {
                item.attrs = JSON.parse(item.attrs)
                // 给每个对象 进行在次遍历 判断有没有空 
                item.attrs.forEach((item1,index)=>{
                   if(item1 == ""){
                       item.attrs[index] = "空"
                   }

                   return item.attrs
                })
            })

            store.commit('changeSpecsList', res.data.list)
        })
    },

    // 请求总数 
    actionSpecsCount(store) {
        reqSpecsCount().then(res => {
            store.commit('changecount', res.data.list[0].total)
        })
    },

    actionSpechPage(store, num) {
        store.commit('changpage', num)
    }
}

// 共用户引用
const getters = {
    // 菜单列表
    specsGetter(state) {
        return state.specslist;
    },
    specsCount(state) {
        return state.number;
    },
    // 设置每页显示的条数
    specsPage(state) {
        return state.pagesize;
    },
    specsnewpage(state) {
        return state.newpage;
    }
}


// 暴露出去
export default {
    state, mutations, actions, getters, namespaced: true
}


